import { Component, OnInit } from '@angular/core';
import { DatabaseService } from '../../../services/database.service';
import { Router, ActivatedRoute } from '@angular/router';
import * as $ from 'jquery';
import { UtilsService } from '../../services/utils.service';
import { FormGroup , FormControl, Validators } from '@angular/forms';
import { first } from 'rxjs/operators';
import { ToastrService } from 'ngx-toastr';
import * as moment from 'moment';
@Component({
  selector: 'app-viaje-programado',
  templateUrl: './viaje-programado.component.html',
  styleUrls: ['./viaje-programado.component.css']
})
export class ViajeProgramadoComponent implements OnInit {
detalle:any;
salidas:any;
slider_imgs:any[]=[];
sus: any;
sus2: any;
sus3: any;
sus4: any;
etiquetas: any;
imagenes: any;
form: FormGroup;
formreg: FormGroup;
permiso: boolean=false;
permiso_form2: boolean=false;
ultimo_dni:string;
id_salida:any;
id_viaje_programado:any;
enviar: boolean=false;

  constructor(
    public db:DatabaseService,
    public route: Router,
    private activatedRoute: ActivatedRoute,
    public utils: UtilsService,
    private toastr: ToastrService
  ) { 
    utils.idioma.subscribe((nextValue) => {
      /* subscribirme */
      this.sus=this.db.getPaginaWebEtiquetas ('viaje_programador_' + nextValue).subscribe ((res) => {
        this.etiquetas = res;
      });
   })
  }
  init() {
    /* Inicializamos el slider */
    var tag = document.createElement('script');
    tag.src = '../../../assets/js/initslider-1.js';
    var firstScriptTag = document.getElementsByTagName('script')[0];
    firstScriptTag.parentNode.insertBefore(tag, firstScriptTag);
  }
  ngOnInit() {

  window.scrollTo(0, 0);

  /* Formularios */
  this.form = new FormGroup ({
    dni: new FormControl ('', Validators.required)
  });

  this.formreg = new FormGroup ({
    nombres: new FormControl ('', Validators.required),
    apelidos: new FormControl ('', Validators.required),
    correo: new FormControl ('',  [Validators.required, Validators.email]),
    telefono: new FormControl ('', Validators.required)
  });
  /* Idioma */

  this.utils.ElIdioma = localStorage.getItem("idioma");

  if (this.utils.ElIdioma === undefined || this.utils.ElIdioma === null) {
    this.utils.ElIdioma = 'es';
  }

  this.sus=this.db.getPaginaWebEtiquetas ('viaje_programador_' + this.utils.ElIdioma).subscribe ((res) => {
    this.etiquetas = res;
  });

  this.sus2=this.db.getPaginaWebEtiquetas ('viaje_programador').subscribe ((res) => {
    this.imagenes = res;
  });

  /* Capturamos el id */
  this.activatedRoute.params.subscribe( params =>{
    this.id_viaje_programado=params['id'];
    this.sus3=this.db.getViajeProgramadoByKey(params['id']).subscribe( (data:any) => 
      {
        this.detalle=data;
        this.slider_imgs=data.imagenes;
        this.init();
        console.log('Este es el detalle:',this.detalle);
        $(document).ready(function() {
          $(".accordion-titulo").click(function(e){
            e.preventDefault();
            var contenido=$(this).next(".accordion-content");
            if(contenido.css("display")=="none"){ //open    
              contenido.slideDown(0);     
              $(this).addClass("open");
            }
            else{ //close   
              contenido.slideUp(0);
              $(this).removeClass("open");  
            }
            });
        });
      });
    
    this.sus4=this.db.getViajeProgramadoSalidas(params['id']).subscribe( (data:any) => 
      {
        this.salidas=data;
        console.log('Estas son todas las salidas:',this.salidas);
      });

  });
    
  }

  async validarDni (){
   
    let dni:any=this.form.value.dni;
    if (dni==null || dni==undefined || dni=='')
    {
      this.toastr.warning('Debe ingresar su Dni.', 'Estimado Ciudadano');
    }
    else
    {
      this.ultimo_dni=String(dni);
      console.log("Este es el Dni=",dni);  
      const usuario: any = await this.db.getUsuarioViajeProgramadoPorDni (String(dni)).pipe (first ()).toPromise ();
      
      if(usuario==null || usuario==undefined)
      {
        this.permiso=true;
        console.log('Usted puede alistarse al viaje programado.');
        this.toastr.success('Usted puede alistarse al viaje programado.', 'Estimado Ciudadano');
      }
      else
      {
        console.log('Respuesta del servidor=',usuario);
        if(usuario.estado==true)
        {
            // Si el estado es true el usuario tiene una sancion activa
            this.permiso=false;
            console.log('Usted posee una sancin activa.');
            this.toastr.error('Usted posee una sancin activa.', 'Estimado Ciudadano');
        }
        else
        {
            this.permiso=true;
            console.log('Usted puede alistarse al viaje programado.');
            this.toastr.success('Usted puede alistarse al viaje programado.', 'Estimado Ciudadano');
        }
      }
      this.form.reset();
    }
    
  }

  ActualizarIdSalida (id:string) {
    console.log ('Este es el id de la salida seleccionada',id);
    this.id_salida=id;
    if(this.ultimo_dni==null || this.ultimo_dni==undefined || this.ultimo_dni==''){
      this.toastr.warning('Debe Ingresar su Dni y darle click al boton Verificar.', 'Estimado Ciudadano');
    }
    else
    {
      $(".salidas").removeClass('btn btn-primary');
      $("#"+id).addClass('btn btn-primary');
      this.permiso=true;
      this.permiso_form2=true;
    }
  }

  async registrarViaje () {
    

    // stop here if form is invalid
    if (this.formreg.invalid) {
      this.toastr.warning('Todos los campos del formulario son requeridos.', 'Estimado Ciudadano');
      this.enviar = false;
      return;
    }
    else
    {
      this.enviar = true;
    }

    if(this.enviar==true)
    {
      let obj: any = {
        id:this.ultimo_dni,
        nombres:this.formreg.value.nombres,
        apelidos:this.formreg.value.apelidos,
        correo:this.formreg.value.correo,
        telefono:this.formreg.value.telefono,
      };
      /* Tenemos que validar que ya no este registrado en este viaje programado */
  
      const validar: any = await this.db.getViajeProgramadoValidarViajero (this.id_viaje_programado,this.id_salida,this.ultimo_dni).pipe (first ()).toPromise ();
      
      if(validar==null || validar==undefined)
      {
        /* Registramos al viajero en la coleccion viajeros */
        this.db.getViajeProgramadoAddViajero(this.id_viaje_programado,this.id_salida,obj).then (() => {
          // Mensaje Toastr
          this.toastr.success('Se a registrado exitosamente a la salida le enviamos un correo con informacin sobre su Viaje Programado.', 'Estimado Ciudadano');
        }).catch (e => {
          console.log('error:'+e);
        })
        
      }
      else
      {
        this.toastr.warning('Usted ya se encuentra registrado en el viaje programado.', 'Estimado Ciudadano');
      }
      this.ultimo_dni='';
      this.permiso=false;
      this.permiso_form2=false;
      $(".salidas").removeClass('btn btn-primary');
      this.formreg.reset();
    }
    
  }

  ObtenerDia (fecha:any) {
    return moment(fecha).format('dddd');
  }
  
  FormatFecha (fecha:any) {
    return moment(fecha).format('L');
  }

  ngOnDestroy() {
    if (this.sus !== null && this.sus !== undefined) {
      this.sus.unsubscribe ();
    }

    if (this.sus2 !== null && this.sus2 !== undefined) {
      this.sus2.unsubscribe ();
    }

    if (this.sus3 !== null && this.sus3 !== undefined) {
      this.sus3.unsubscribe ();
    }

    if (this.sus4 !== null && this.sus4 !== undefined) {
      this.sus4.unsubscribe ();
    }

  }

}
